import React from 'react';
import Talent from './talent.js'
import Character from "./data/fischl.js"


export default class TalentInfo extends React.Component {
    render() {
      var i = 0
      return (
        <div className="TalentInfo">
            <div><p className="subtitle">Talents</p></div>
            {Character.talents.map((ctalent) => {
              return <div key={ctalent.name}><Talent talent={ctalent} characterStats={this.props.characterStats} talentLevel={this.props.characterStats.talentLevel[i++] - 1}/></div>
            })}
        </div>
      );
    }
  }