import React from 'react';
import {ArtifactSlots, Sets} from "./data/artifacts.js";
import Artifact from "./artifact.js";

export default class Artifacts extends React.Component {
    constructor(props) {
        super(props);
        this.state = {sets: new Map()}

        this.setChange = this.setChange.bind(this)
      }
      setChange(oldSet,newSet) {
        let sets = this.state.sets;
        if (oldSet in sets)
            sets[oldSet] -= 1
        if (newSet in sets)
            sets[newSet] += 1
        else 
            sets[newSet] = 1
        this.setState({sets: sets});

        let bonuses = new Map()
        for(var setId in sets){
            var set = Sets[setId]
            for(var i = 1 ; i <= sets[setId]; ++i){
                if (set.hasOwnProperty('bonus' + i)) {
                    let bonus = set['bonus' + i]
                    for (var index = 0; index < bonus.length; index++) { 
                        bonuses[bonus[index].stat] = bonus[index].value;
                    } 
                }
            }
        }
        this.props.updateStats('setBonuses',bonuses)
    }
    render() {
      return (
            <div className="PrimarySection">
            {
                ArtifactSlots.map((slot) => {
                    return <div key={slot.name} className="BoxInfo"><Artifact decodedData={this.props.decodedData} slot={slot} updateStats={this.props.updateStats} setCount={this.state.sets} setChange={this.setChange} encodeList={this.props.encodeList}/></div>
                })
            }
            </div>
      );
    }
}