import React from 'react';
import STATS from './enums/stats.js'
import {Sets} from './data/artifacts.js'
import UnknownStat from "./unknownstat.js";
import DataEncoder from './util/dataencoder.js'

export default class Artifact extends React.Component {
    constructor(props) {
        super(props);
        
        let decoded = this.decodeState(this.props.decodedData)
        this.state = {set: decoded.set, stats: decoded.stats, subStatList: [STATS.NONE, STATS.HP, STATS.ARTIFACTATK, STATS.DEF, STATS.HPPER, STATS.ATKPER, STATS.DEFPER, STATS.MASTERY, STATS.RECHARGE, STATS.CRITC, STATS.CRITD, STATS.HEAL] }

        this.valueChanged = this.valueChanged.bind(this);
        this.statChanged = this.statChanged.bind(this);
        this.setChange = this.setChange.bind(this)
        this.encodeState = this.encodeState.bind(this)

        this.props.encodeList.push(this.encodeState)
    }
    encodeState(){
        let stats = this.state.stats;
        console.log(stats)
        var data = [this.state.set,
                    ((stats[0].stat) << 4) | stats[1].stat,
                    ((stats[2].stat) << 4) | stats[3].stat,
                    stats[4].stat];
        for(let i = 0; i < 5; ++i)
            data = data.concat(DataEncoder.encodeFloat(this.state.stats[i].value))
        return data
    }
    decodeState(data){
        let stats = [{stat: 0},{stat: 0},{stat: 0},{stat: 0},{stat: 0}]
        if (data.length < 14) return {set: 0, stats: stats};
        let set = data[0]
        stats[0].stat = data[1] >> 4
        stats[1].stat = data[1] & 0xF
        stats[2].stat = data[2] >> 4
        stats[3].stat = data[2] & 0xF
        stats[4].stat = data[3]
        for(let i = 0; i < 5; ++i){
            stats[i].value = DataEncoder.decodeFloat(data[4 + i * 2],data[5 + i * 2])
        }
        data.splice(0,14)

        this.props.updateParent(stats)

        return {set: set, stats: stats}
    }
    valueChanged(id,value){
        var cStats = this.state.stats;
        cStats[id].value = value;
        this.setState({stats: cStats})
        this.updateParent(cStats)
    }
    statChanged(id,stat){
        var cStats = this.state.stats;
        cStats[id].stat = stat;
        this.setState({stats: cStats})
        this.updateParent(cStats)
    }
    updateParent(stats){
        let statMap = new Map()
        for(let i = 0; i < stats.length; ++i){
            let o = stats[i]
            let statName
            if (i === 0)
                statName = this.props.slot.mainStat[o.stat]
            else
                statName = this.state.subStatList[o.stat]
            if (o.hasOwnProperty("stat") && o.hasOwnProperty("value")){
                if (statName in statMap)
                    statMap[statName] += parseFloat(o.value) | 0
                else
                    statMap[statName] = parseFloat(o.value) | 0
            }
        }
        this.props.updateStats(this.props.slot.id,statMap)
    }
    setChange(event) {
        var val = event.target.value;
        this.props.setChange(this.state.set,val);
        this.setState({set: val});
    }
    
    mapSets(){
        var i = 0
        return Sets.map((set) => {
          return <option key={set.name} value={i++}>{set.name}</option>
        })
    }
    getSetBonuses(set,count){
        let bonuses = []
        for(var i = 1 ; i <= count; ++i){
            if (set.hasOwnProperty('bonus' + i)) {
                let bonus = set['bonus' + i]
                bonuses.push(
                <div key={i}>{
                    bonus.map((stat) => {
                        return <div key={stat.stat}>{stat.stat} : {stat.value}</div>
                    })}
                </div>
                )
            }
        }
        return bonuses;
    }

    render() {
        const setCount = this.props.setCount[this.state.set] || 0;
        return (
        <div className="Artifact">
            <div><p className="subtitle2">{this.props.slot.name}</p></div>
            <div className="SetPicker">
                <div>Set: 
              <select name="SetPicker" value={this.state.set} onChange={this.setChange}>
                  {this.mapSets()}
              </select>
              </div>
            </div>
            <UnknownStat  id={0} statList={this.props.slot.mainStat} stat={this.state.stats[0].stat} value={this.state.stats[0].value} valueChanged={this.valueChanged} statChanged={this.statChanged}/>
            <UnknownStat  id={1} statList={this.state.subStatList} stat={this.state.stats[1].stat} value={this.state.stats[1].value}  valueChanged={this.valueChanged} statChanged={this.statChanged}/>
            <UnknownStat  id={2} statList={this.state.subStatList} stat={this.state.stats[2].stat} value={this.state.stats[2].value}  valueChanged={this.valueChanged} statChanged={this.statChanged}/>
            <UnknownStat  id={3} statList={this.state.subStatList} stat={this.state.stats[3].stat} value={this.state.stats[3].value}  valueChanged={this.valueChanged} statChanged={this.statChanged}/>
            <UnknownStat  id={4} statList={this.state.subStatList} stat={this.state.stats[4].stat} value={this.state.stats[4].value}  valueChanged={this.valueChanged} statChanged={this.statChanged}/>
            <div>
                <p>Pieces In Set: {setCount}</p>
                <p>Active Effects:</p>
                <div>
                    {this.getSetBonuses(Sets[this.state.set],setCount)}
                </div>
            </div>
        </div>
      );
    }
}