import React from 'react';
import STATS from "./enums/stats.js";
import Stat from "./stat.js";
import UnknownStat from "./unknownstat.js";
import DataEncoder from './util/dataencoder.js';

export default class WeaponInfo extends React.Component {
    
    constructor(props) {
      super(props);
      let decoded = this.decodeState(this.props.decodedData)
      this.state = {atk: decoded.atk, secondaryValue: decoded.secondaryValue, secondaryStat: decoded.secondaryStat,
        statList: [STATS.NONE,STATS.ATKPER, STATS.HPPER, STATS.CRITD, STATS.CRITC, STATS.RECHARGE, STATS.MASTERY]}
      this.atkValueChanged = this.atkValueChanged.bind(this)
      this.secondaryValueChanged = this.secondaryValueChanged.bind(this)
      this.secondaryStatChanged = this.secondaryStatChanged.bind(this)
      this.encodeState = this.encodeState.bind(this)

      this.props.encodeList.push(this.encodeState)
    }
    encodeState(){
      let data = DataEncoder.encodeInt(this.state.atk)
      data.push(this.state.secondaryStat)
      data = data.concat(DataEncoder.encodeFloat(this.state.secondaryValue))
      return data
    }
    decodeState(data){
      if (data.length < 4) return {atk: 0, secondaryValue: 0, secondaryStat: 0};
      let decoded = {atk: DataEncoder.decodeInt(data[0],data[1]),
                     secondaryStat: data[2],
                     secondaryValue: DataEncoder.decodeFloat(data[3],data[4])}
      data.splice(0,5)
      
      this.props.updateStats(this.props.statItemId,
        this.generateStatMap(decoded.atk,this.state.statList[decoded.secondaryStat],decoded.secondaryValue))

      return decoded
    }
    atkValueChanged(val){
      this.setState({atk: val});
      this.props.updateStats(this.props.statItemId,
        this.generateStatMap(val,this.state.statList[this.state.secondaryStat],this.state.secondaryValue))
    }
    secondaryValueChanged(id,value){
      this.setState({secondaryValue: value})
      this.props.updateStats(this.props.statItemId,
        this.generateStatMap(this.state.atk,this.state.statList[this.state.secondaryStat],value))
    }
    secondaryStatChanged(id,stat){
      this.setState({secondaryStat: stat})
      this.props.updateStats(this.props.statItemId,
        this.generateStatMap(this.state.atk,this.state.statList[stat],this.state.secondaryValue))
    }
    generateStatMap(atk,secondaryStat,secondaryVal){
      let m = new Map()
      m[STATS.ATK] = parseFloat(atk) || 0;
      if (secondaryStat === STATS.ATK)
        m[STATS.ATK] += parseFloat(secondaryVal) || 0;
      else
        m[secondaryStat] = parseFloat(secondaryVal) || 0;
      return m;
    }
      render() {
        return (
          <div className="WeaponInfo">
              <div>
                  <p className="subtitle2">Weapon</p></div>
              <Stat stat={STATS.ATK} value={this.state.atk} valueChanged={this.atkValueChanged}/>
              <UnknownStat id={0} statList={this.state.statList} stat={this.state.secondaryStat} value={this.state.secondaryValue}
              valueChanged={this.secondaryValueChanged} statChanged={this.secondaryStatChanged}/>
          </div>
        );
      }
  }