class DataEncoder {
    static encodeFloat(float){
        console.log(float)
        let int = Math.floor(float * 10)
        console.log(int)
        return [(int & 0xFF00) >> 8, int & 0xFF]
    }
    static decodeFloat(part1, part2){
        return (parseInt((part1 << 8) | part2) | 0) / 10
    }
    static encodeInt(int){
        return [(int & 0xFF00) >> 8, int & 0xFF]
    }
    static decodeInt(part1, part2){
        return ((part1 << 8) | part2) | 0
    }
    addItem(key,val){
        if(key in this.idMap)
            this.data[this.idMap[key]] = val;
        else{
            this.idMap[key] = this.data.length
            this.data.push(val)
        }
    }
    encode(data){
        var binary = ""
        var len = data.length
        for (let i = 0; i < len; i++) {
            var val = data[ i]
            if (val < 0) {
                console.log("Error: value < 0 cannot add to url")
                val = 0
            }
            else if (val > 255) {
                console.log("Error: value > 255 cannot add to url")
                val = 255
            }
            binary += String.fromCharCode(val);
        }
        return window.btoa(binary)   
    }
    decode(str){
        let decoded = []
        try {
            decoded = window.atob(str)
        }catch(e){
            console.log("Error: Invalid Build String")
        }
        let len = decoded.length
        let data = []
        for(let i = 0; i < len; ++i){
            data.push(decoded.charCodeAt(i))
        }
        return data;
    }
}
export default DataEncoder;