import React from 'react';
import isNumberKey from './util/isnumberkey.js';

export default class Stat extends React.Component {
    constructor(props) {
      super(props);
      
      this.state = {value: this.props.value};
      this.valueChanged = this.valueChanged.bind(this);
    }
    valueChanged(event) {
      var val 
      if (isNumberKey(event.target.value))
        val = event.target.value;
      else
        val = parseFloat(event.target.value) || '';
      this.setState({value: val});
      this.props.valueChanged(val);
    }
      render() {
        return (
          <div className="Stat">
              <div className="NumberLabel">{this.props.stat}</div>
              <input type="text" className="EntryBox" value={this.state.value} onChange={this.valueChanged}/>
          </div>
        );
      }
  }