import React from 'react';
import isNumberKey from './util/isnumberkey.js';

export default class UnknownStat extends React.Component {
    constructor(props) {
      super(props);
      
      this.state = {value: this.props.value || 0, stat: this.props.stat || 0};
      this.valueChanged = this.valueChanged.bind(this);
      this.statChanged = this.statChanged.bind(this)
    }
    valueChanged(event) {
        var val 
        if (isNumberKey(event.target.value))
          val = event.target.value;
        else
          val = parseFloat(event.target.value) || '';
        this.setState({value: val});
        this.props.valueChanged(this.props.id,val);
    }
    statChanged(event) {
      var val = parseInt(event.target.value) || 0;
      this.setState({stat: val});
      this.props.statChanged(this.props.id,val);
    }
    mapOptions(statList){
      let i = 0;
      return statList.map((stat) => {
        return <option key={stat}value={i++}>{stat}</option>
    })}
    render() {
      return (
        <div className="Stat">
            <select name="StatType" className="NumberLabel" value={this.state.stat} onChange={this.statChanged}>
                {this.mapOptions(this.props.statList)}
            </select>
            <input type="text" className="EntryBox" value={this.state.value} onChange={this.valueChanged}/>
        </div>
      );
    }
  }