import React from 'react';
import CharacterStats from "./characterstats.js";
import CharacterInfo from "./characterinfo.js";
import WeaponInfo from "./weaponinfo.js";
import Artifacts from "./artifacts.js";
import AggregatedStats from "./aggregatedstats.js"
import TalentInfo from "./talentinfo.js"
import DataEncoder from './util/dataencoder.js'
const URL = "https://daynar100.github.io/GenshinImpactCharacterBuilder/?"

export default class Loadout extends React.Component {
    constructor(props) {
      super(props);
      let str = window.location.search
      str = str.substring(str.indexOf('?') + 1,str.length)
      this.state = {characterStats: new CharacterStats(), buildUrl:'', encodeList:[], decodedData: (new DataEncoder()).decode(str)};
      console.log(this.state.decodedData)
      this.talentLevelChange = this.talentLevelChange.bind(this)
      this.generateUrl = this.generateUrl.bind(this)
    }
    generateUrl(){
      let data = [];
      let size = this.state.encodeList.length;
      for(let i = 0; i < size; ++i){
        data = data.concat(this.state.encodeList[i]())
      }
      console.log(data)
      this.setState({buildUrl: URL + ((new DataEncoder()).encode(data))})
    }
    updateCharacterStats = (itemId,stats) => {
      let cs = this.state.characterStats;
      cs.updateItem(itemId,stats);
      this.setState({characterStats: cs});
    }
    talentLevelChange(number,value){
      let cs = this.state.characterStats;
      cs.talentLevel[number] = value;
      this.setState({characterStats: cs});
    }
    render() {
      return (
        <div className="Loadout">
            <BuildInfo />
          <div className="Loadout-body">
            <div className="PrimarySection">
              <div className="BoxInfo">
                <div className="UrlGenerator">
                  <button type="button" onClick={this.generateUrl}>Generate URL</button>
                  {document.queryCommandSupported('copy') && 
                  <button type="button" onClick={(event) => {
                    const el = document.createElement('textarea')
                    el.value = this.state.buildUrl
                    document.body.appendChild(el)
                    el.select()
                    document.execCommand('copy')
                    document.body.removeChild(el)
                  }}>Copy</button>}
                  <p className="GeneratedUrl">{this.state.buildUrl}</p>
                </div>
              </div>
            </div>
            <div className="subtitle">Character Info</div>
            <div className="PrimarySection">
              <div className="BoxInfo">
                <CharacterInfo statItemId="character" decodedData={this.state.decodedData} updateStats={this.updateCharacterStats} talentLevelChange={this.talentLevelChange} encodeList={this.state.encodeList}/>
              </div>
              <div className="BoxInfo">
                <WeaponInfo statItemId="weapon" decodedData={this.state.decodedData} updateStats={this.updateCharacterStats} encodeList={this.state.encodeList}/>
              </div>
            </div>
            <div className="subtitle">Artifacts</div>
            <Artifacts decodedData={this.state.decodedData} updateStats={this.updateCharacterStats} encodeList={this.state.encodeList}/>
            <div className="subtitle2"><p>The following sections are works in progress. Don't trust them.</p></div>
            <div className="subtitle">Stats</div>
            <div className="PrimarySection">
              <div className="BoxInfo">
            <AggregatedStats characterStats={this.state.characterStats}/>
            </div>
            </div>
            <TalentInfo characterStats={this.state.characterStats}/>
          </div>
        <div>
          <p>
            This site is not affiliated with or endorsed by miHoYo.
          </p>
        </div>
        </div>
      );
    }
}

class BuildInfo extends React.Component {
    render() {
      return (
        <div className="BuildInfo">
            {/*Build Name: 
          <input type="text"/>*/}
          <p className="title">Genshin Impact Character Builder</p>
        </div>
      );
    }
}