import STATS from "./enums/stats.js";
export default class CharacterStats {
    statMap = new Map();
    character = new Map();
    weapon = new Map();
    hat = new Map();
    goblet = new Map();
    clock = new Map();
    flower = new Map();
    feather = new Map();
    setBonuses = new Map()
    talentLevel = [1,1,1]

    getTotalAtk(){
        return Math.floor(this.getStat(STATS.ATK) * (this.getStat(STATS.ATKPER) / 100 + 1) + this.getStat(STATS.ARTIFACTATK))
    }
    getStat(name){
        if (name in this.statMap)
            return parseFloat(this.statMap[name]) || 0;
        else
            return 0;
    }
    updateItem(name,value) {
        if (this.hasOwnProperty(name)){
            this.removeItemFromStatMap(this[name]);
            this[name] = value;
            this.addItemToStatMap(this[name]);
        }
    }
    removeItemFromStatMap(map){
        for (var key in map) {       
            var value = map[key];
            if (key in this.statMap)
                this.statMap[key] -= parseFloat(value) || 0; 
        }
    }
    addItemToStatMap(map){
        for (let key in map) {       
            var value = map[key];
            if (key in this.statMap)
                this.statMap[key] += parseFloat(value) || 0; 
            else
                this.statMap[key] = parseFloat(value) || 0; 
        }
    }
}